import React, { useContext } from "react";
import Usersingleitem from "./Usersingleitem";
import DesignsContext from "../../context/designs/designsContext";

import { Stack, Text, FontIcon } from "@fluentui/react";

function Useritems() {
  //============================================================================
  const designsContext = useContext(DesignsContext);
  const { files, folder, setCurrent } = designsContext;
  //============================================================================
  //---------------------------------------------------------------------------
  const dateTextStyles: ITextStyles = {
    root: {
      color: "#505050",
      fontWeight: 600,
      margin: "auto",
    },
  };
  //---------------------------------------------------------------------------
  const iconClass = {
    fontSize: 25,
    height: 25,
    width: 25,
    margin: "7px 12px 0px 10px",
  };
  //---------------------------------------------------------------------------

  return (
    <Stack
      tokens={{}}
      styles={{
        root: {
          height: window.innerHeight - 36,
          overflowY: "scroll",
          overflowX: "hidden",
        },
      }}
      onClick={() => setCurrent()}
    >
      <Stack
        horizontal
        styles={{ root: { height: 37, justifyContent: "center" } }}
      >
        <Stack.Item>
          <FontIcon
            iconName={(function () {
              switch (folder) {
                case 0:
                  return "DocumentSet";
                /*case 1:
                  return "FabricOpenFolderHorizontal";*/
                case 1:
                  return "PeopleRepeat";
                case 2:
                  return "GlobeFavorite";
                case 3:
                  return "RecycleBin";
                default:
                  return "Error";
              }
            })()}
            style={iconClass}
          />
        </Stack.Item>
        <Text variant="large" styles={dateTextStyles}>
          {(function () {
            switch (folder) {
              case 0:
                return "My documents";
              /*case 1:
                return "Recently opened";*/
              case 1:
                return "Shared with me";
              case 2:
                return "Public examples";
              case 3:
                return "Trash";
              default:
                return "Loading!";
            }
          })()}
        </Text>
      </Stack>
      <Stack
        horizontal
        wrap
        tokens={{
          childrenGap: 10,
        }}
        style={{
          root: {
            overflowY: "scroll",
            overflowX: "hidden",
            paddingRight: 10,
          },
        }}
      >
        {files.map((doc) => (
          <Usersingleitem key={doc.key} doc={doc} width={250} />
        ))}
      </Stack>
    </Stack>
  );
}

export default Useritems;
